.ui-blockquote {
  clear: both;
}

.prismic-raw-html {
  width: 100%;
  overflow: auto;
  clear: both;
}
.prismic-raw-html table {
  overflow-x: auto;
  margin-right: -15px;
  padding-right: 15px;
  box-sizing: content-box;
  font-size: 13px;
  border-collapse: collapse;
  border-spacing: 0;
  margin-bottom: 48px;
}
.prismic-raw-html table td,
.prismic-raw-html table th {
  text-align: left;
  min-width: 120px;
  padding-right: 12px;
  padding-top: 12px;
  padding-bottom: 12px;
}
.prismic-raw-html table td:last-child,
.prismic-raw-html table th:last-child {
  padding-right: 0;
}
.prismic-raw-html table th,
.prismic-raw-html table b {
  font-weight: 600;
}
.prismic-raw-html table tbody tr td {
  border-top: 1px solid #DEE3EA;
}
.prismic-raw-html table tbody tr:first-child td {
  border-top: none;
}
.prismic-raw-html table > thead > tr > th {
  border-bottom: 1px solid #E9EDF3;
  font-weight: 600;
}

.prismic-ad {
  display: block;
  margin: var(--space-9) 0 var(--space-10);
  clear: both;
}
.prismic-ad a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: var(--space-6) 368px var(--space-6) var(--space-6);
  border: var(--border-regular);
  border-color: var(--c-indigo-30);
  border-radius: var(--radius-2);
  overflow: hidden;
  position: relative;
  min-height: 225px;
  transition: 0.2s box-shadow ease-out;
}
.prismic-ad a .ui-paragraph {
  margin-bottom: 0;
}
.prismic-ad a .prismic-ad__image {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  max-width: 337px;
  object-fit: cover;
  z-index: -1;
}
.prismic-ad a:hover {
  box-shadow: var(--elevation-3);
}
.prismic-ad .ui-heading {
  color: var(--c-indigo-100);
  margin-bottom: var(--space-2);
}
@media (max-width: 1023px) {
  .prismic-ad a {
    padding-right: var(--space-6);
  }
  .prismic-ad a .prismic-ad__image {
    opacity: 0.25;
  }
}

.prismic-youtube-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  max-width: 100%;
  clear: both;
}

.prismic-youtube-container iframe,
.prismic-youtube-container object,
.prismic-youtube-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.prismic-floating-image {
  width: 295px;
  float: right;
  margin: 8px -20px 32px 74px;
}
.prismic-floating-image.left {
  float: left;
  margin-right: 74px;
  margin-left: -20px;
}
.prismic-floating-image img {
  box-shadow: var(--elevation-8);
}
.prismic-floating-image figcaption {
  color: var(--c-carbon-20);
  font-size: 11px;
  margin-top: 13px;
}
@media (max-width: 1023px) {
  .prismic-floating-image {
    margin-right: 0;
    margin-left: 32px;
    width: 100%;
  }
  .prismic-floating-image.left {
    margin-left: 0;
    margin-right: 32px;
  }
}

:host {
  padding-bottom: 76px;
}

.heading-group img {
  max-width: 100%;
  margin-block-end: var(--space-11);
}
.heading-group .ui-heading-1 {
  margin-block-start: var(--space-10);
  margin-block-end: var(--space-5);
}
.heading-group .ui-paragraph {
  color: #73849A;
  margin-block-end: var(--space-6);
}

resource-toc {
  margin-block-start: var(--space-10);
}

resource-author-item {
  margin-block-start: var(--space-3);
  margin-block-end: var(--space-3);
}
resource-author-item:last-of-type {
  margin-block-end: 73px;
}

article .ui-heading-2 {
  margin-block-start: var(--space-9);
  margin-block-end: var(--space-3);
}
article .ui-heading-3 {
  margin-block-start: var(--space-8);
  margin-block-end: var(--space-3);
}
article .ui-heading-4 {
  margin-block-start: var(--space-3);
  margin-block-end: var(--space-2);
}
article .ui-heading:first-child {
  margin-block-start: 0;
}
article p {
  font-size: 16px;
  letter-spacing: -0.011em;
  line-height: 1.75em;
  margin-bottom: 1.8em;
  color: var(--c-indigo-80);
}
article ul li {
  list-style-type: none;
}
article ul li::before {
  content: "";
  display: inline-block;
  width: 6px;
  height: 6px;
  background: #b2becd;
  position: absolute;
  top: 12px;
  left: 0;
  border-radius: 8px;
}
article ol li {
  list-style-type: counter-style;
}
article ul,
article ol {
  font-size: 16px;
  letter-spacing: -0.011em;
  line-height: 1.75em;
  margin-bottom: 1.8em;
  color: var(--c-indigo-80);
}
article li {
  position: relative;
  padding-left: 18px;
}
article table {
  overflow-x: auto;
  margin-right: -15px;
  padding-right: 15px;
  box-sizing: content-box;
  font-size: 13px;
}
article table td,
article table th {
  min-width: 120px;
  padding-right: 12px;
}
article table tbody tr td {
  border-top-color: #DEE3EA;
}
article table > thead > tr > th {
  border-bottom-color: #E9EDF3;
  font-weight: 600;
}
article img {
  max-width: 100%;
}